}

.nav-toggle__icon {
  border-bottom: 6px solid #d71e21;
  display: block;
  transition: border-color .25s;
  transition-delay: .15s;
}

.nav-toggle__icon:before, .nav-toggle__icon:after {
  border-top: 6px solid #d71e21;
  content: "";
  position: absolute;
  left: 0;
  right: 0;
}

.nav-toggle__icon:before {
  top: 4px;
}

.nav-toggle__icon:after {
  bottom: 4px;
  transition-property: transform, border-color;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.5, -0.5, 0.5, 1.5);
}

.nav-toggle__icon:before, .nav-toggle__icon:after {
  transition-property: transform, border-color;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.5, -0.5, 0.5, 1.5);
  transition-delay: 0.125s;
}

.nav-toggle__icon:before, .nav-toggle__icon:after {
  transition-delay: 0s;
  transform-origin: center;
}

.nav-toggle.is-active .nav-toggle__icon {
  transition-delay: 0s;
  border-bottom-color: transparent;
}

.nav-toggle.is-active .nav-toggle__icon:before, .nav-toggle.is-active .nav-toggle__icon:after {
  transition-delay: 0.125s;
}

.nav-toggle.is-active .nav-toggle__icon:before {
  transform: translateY(13px) rotate(135deg);
  border-color: #d71e21;
}

.nav-toggle.is-active .nav-toggle__icon:after {
  transform: translateY(-13px) rotate(-135deg);
}

