  padding: 60px 0 63px;
}

.category {
  position: relative;
}

.category::after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  content: '';
  display: block;
  background: url("../img/bg/sect-bg.jpg") repeat-x 0 0;
  height: 12px;
}

.category__in {
  margin: 75px 0 0;
  *zoom: 1;
}

.category__in:after {
  content: " ";
  display: table;
  clear: both;
}

.category__list {
  float: left;
  margin-right: 30px;
  width: 350px;
}

.category__list-title {
  padding: 0 0 20px;
  color: #000;
  font-size: 22px;
  line-height: 1;
  font-weight: 700;
  border-bottom: 1px solid #e6e7e8;
}

.category__list-links {
  padding: 42px 20px 42px 0;
}

.category__list-item:not(:last-child) {
  margin-bottom: 15px;
}

.category__list-item.slick-current .category__list-text {
  color: #de4e4e;
  border-bottom-color: #de4e4e;
}

.category__list-text {
  display: inline-block;
  cursor: pointer;
  border-bottom: 1px solid rgba(77, 77, 79, 0.7);
  transition: border-bottom-color .2s, color .2s;
  line-height: 19px;
}

.no-touch .category__list-text:hover {
  color: #de4e4e;
  border-bottom-color: #de4e4e;
}

.category__list-btn {
  position: absolute;
  padding: 5px;
  background: transparent;
  transform: rotate(90deg);
}

.category__list-btn.slick-disabled {
  visibility: hidden;
}

.category__list-btn_prev {
  left: 15px;
  top: -10px;
}

.category__list-btn_next {
  bottom: 0;
  left: 15px;
}

.category__list-btn-icon {
  font-size: 44px;
  stroke: #000;
}

.category__view {
  float: left;
  width: calc(100% - 380px);
}

.category__view-item {
  height: 300px;
  overflow: hidden;
}

.category__view-item img {
  transition: all 500ms ease-in-out;
  transform: scale(1.25, 1.25);
}

.category__view-item.slick-active img {
  transform: scale(1, 1);
}

.category__view-btn {
  margin-top: 20px;
  margin-left: 57px;
  padding: 0 27px;
}

.category__list-links, .category__view-pic {
  display: none;
}

.category__list-links.slick-initialized, .category__view-pic.slick-initialized {
  display: block;
}

.category_default {
  padding: 75px 0 105px;
}

.articles {
  padding: 45px 0 60px;
}

.articles__container {
  display: flex;
  justify-content: space-between;
}

.articles__col {
  flex: 0 1 825px;
}

.article__title {
  text-align: left;
}

.article__in {
  margin: 25px 0 28px;
  *zoom: 1;
}

.article__in:after {
  content: " ";
  display: table;
  clear: both;
}

.article__in p {
  line-height: 1.2;
}

.article__in p:not(:last-child) {
  margin-bottom: 20px;
}

.article__img-wrap {
  margin-right: 30px;
  float: left;
  width: 65.45455%;
}
